import React from "react";
import Classes from "./Classes";
import "./Home.css";
function Home() {
  return (
    <div className="Home ">
      <div className="container-fluid ">
          <div className="row">
        <div className="col-12 bg-white d-flex m-auto">
            <div className="leftside">
            <div
              className=" mt-5 ms-3"
              style={{ textTransform: "capitalize" }}
            >
              <h2>
                <span className="bg-warning "> the Best online soloution</span>
                <br />{" "}
              </h2>
              <h2>
                {" "}
                <span className="bg-warning mt-1">for learning recitation</span>
              </h2>
              <div className=" shadow-lg bg-white col-7 p-3">
                <div className="header-text mb-4">
                  <h5 style={{ fontWeight: "bold" }}>
                    Boook your Free Classes
                  </h5>
                  <div class="form-check d-flex form-switch">
                    <p className="m-0 me-1"> for Kids </p>
                    <input
                      class="form-check-input mx-0"
                      type="checkbox"
                      role="switch"
                      id="flexSwitchCheckDefault"
                    />
                    <label
                      class="form-check-label ms-1"
                      for="flexSwitchCheckDefault"
                    >
                      for 18+
                    </label>
                  </div>
                </div>
                <div className="container m-auto mb-5 d-flex rounded flex-wrap">
                  <Classes idx={1} />
                </div>
                <button className="btn-warning py-3  text-light btn trial col-12 rounded-0 ">
                  Schedule a Free Class
                </button>
              </div>
            </div>
            </div>
            <div
              id="carouselExampleIndicators"
              class="carousel slide m-auto"
              data-ride="carousel"
            >
              <ol class="carousel-indicators">
                <li
                  data-target="#carouselExampleIndicators"
                  data-slide-to="0"
                  class="active"
                ></li>
                <li
                  data-target="#carouselExampleIndicators"
                  data-slide-to="1"
                ></li>
                <li
                  data-target="#carouselExampleIndicators"
                  data-slide-to="2"
                ></li>
              </ol>
              <div class="carousel-inner">
                <div class="carousel-item active">
                  <img class="d-block w-100 " src="./Imges/home-image.jpg" alt="First slide" />
                </div>
                <div class="carousel-item">
                  <img class="d-block w-100 " src="./Imges/home-image.jpg" alt="Second slide" />
                </div>
                <div class="carousel-item">
                  <img class="d-block w-100 " src="./Imges/home-image.jpg" alt="Third slide" />
                </div>
              </div>
              <a
                class="carousel-control-prev"
                href="#carouselExampleIndicators"
                role="button"
                data-slide="prev"
              >
                <span
                  class="carousel-control-prev-icon"
                  aria-hidden="true"
                ></span>
                <span class="sr-only">Previous</span>
              </a>
              <a
                class="carousel-control-next"
                href="#carouselExampleIndicators"
                role="button"
                data-slide="next"
              >
                <span
                  class="carousel-control-next-icon"
                  aria-hidden="true"
                ></span>
                <span class="sr-only">Next</span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;
