import React from "react";
import "./Feature.css";
function Feature() {
  const features=[
    { icon:"uil uil-bag",name:"More career opportunity"},
    { icon:"uil uil-users-alt",name:"providing knowladgen on cooperative plateforms"},
    { icon:"uil uil-book-alt",name:"Structure tailored lesson"},
    { icon:"uil uil-language",name:"improve Knowladge skills "},
    { icon:"uil uil-graduation-cap",name:"learn effectively"},
    { icon:"uil uil-globe",name:"global learning enviroment"}
  ]
  return (
    <>
      <div className="feature  mb-5">
        <div className="container-fluid">
          <div className="col-11 m-auto">
            <div className="row bg-white shadow ">
              <div style={{backgroundColor:" rgba(0, 0, 0, 0.49)"}} className="feature-body card  p-3  text-center ">
                <h1 style={{ textTransform: "uppercase" }}>
                  Some Feature lagma is Proud of
                </h1>
              </div>
                <div class="row feature-body row-cols-3 pb-5 pt-4">
                {features.map((data,key)=>(  <div className="col ">
                    <div className="col text-center m-1 p-3 py-5 border rounded" style={{backgroundColor: "#0000007d"}}> 
                    <i class={data.icon}></i>
                    <div>{data.name}</div>
                    </div>
                  </div>
                 ))}
                </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Feature;